import React from "react";
import "./App.scss";
import WebFont from "webfontloader";
import ReactGA from 'react-ga'

import { Container, Row, Col, Nav, NavLink, Navbar } from "react-bootstrap";
import { Link } from "react-scroll";
import TopTitleContainer from "./pages/TopTitleContainer/TopTitleContainer";
import ClientsContainer from "./pages/ClientsContainer/ClientsContainer";
import ProjectsContainer from "./pages/ProjectsContainer/ProjectsContainer";

function App() {
  ReactGA.initialize('UA-198821577-1', []);
  ReactGA.event({
    category: 'load',
    action: 'site_loaded',
    label: 'loaded'
  });
  WebFont.load({
    google: {
      families: ["Dosis", "KoHo"],
    },
  });
  return (
    <div className="App">
      <Container fluid>
        <Navbar bg="dark" variant="dark" expand="lg" sticky="top" className="custom-navbar">
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
              <Nav.Item>
                <Nav.Link>
                  <Link
                    activeClass="active"
                    to="aboutContainer"
                    spy={true}
                    smooth={true}
                  >
                    About
                  </Link>
                </Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link>
                  <Link
                    activeClass="active"
                    to="projectsContainer"
                    spy={true}
                    smooth={true}
                  >
                    Projects
                  </Link>
                </Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link>
                  <Link
                    activeClass="active"
                    to="clientsContainer"
                    spy={true}
                    smooth={true}
                  >
                    Clients
                  </Link>
                </Nav.Link>
              </Nav.Item>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <Row>
          <Col>
            <TopTitleContainer></TopTitleContainer>
          </Col>
        </Row>
        <Row>
          <Col>
            <ProjectsContainer></ProjectsContainer>
          </Col>
        </Row>
        <Row>
          <Col>
            <ClientsContainer></ClientsContainer>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default App;
