import React from "react";
import "./ClientsContainer.scss";
import appMobi from "../../assets/png/appMobi.png";
import bakerHughes from "../../assets/png/baker_hughes.png";
import ge from "../../assets/png/ge.png";
import intel from "../../assets/png/intel.png";

export default class ClientsContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dimensions: {},
    };
  }
  /* Lifecycle Events */
  componentDidMount() {
    this.setState({
      dimensions: {
        height: window.screen.height,
      },
    });
  }
  render() {
    const { dimensions } = this.state;
    return (
      <div id="clientsContainer" className="clients-container">
        <div className="common-title-container">
          <span>Clients</span>
        </div>
        <div className="clients-content">
          <div className="clients">
            <img className="small" src={appMobi} alt="Appmobi" />
            <img src={intel} alt="Intel" />
            <img src={ge} alt="GE" />
          </div>
          <div className="clients">
            <img src={bakerHughes} alt="Baker Hughes" />
          </div>
        </div>
      </div>
    );
  }
}
