import React from "react";
import "./TopTitleContainer.scss";

import TypedText from '../../components/TypedText/TypedText';
import AnimateTextContainer from "../../components/AnimateTextContainer/AnimateTextContainer";

export default class TopTitleContainer extends React.Component {
  constructor(props) {
    super(props);
    this.description =
      "I've been programming since I finished my bachelor's degree in 2009, and it's never been anything but exciting." +
      " My journey started with a small-scale iPhone app, and now I'm proud to say that I've successfully introduced large-scale web applications for corporations." +
      " Currently working for the client Baker Hughes as a Lead Frontend Developer.";
  }
  /* Lifecycle Evenets */
  componentDidMount() {
  }
  componentWillUnmount() {
    // Please don't forget to cleanup animation layer
    this.typed.destroy();
  }
  render() {
    return (
      <div id="aboutContainer" className="top-title-container container-fluid">
        <div className="row title-text">
          <div className="animated-heading centered col-xs-12 col-md-4">
            <AnimateTextContainer></AnimateTextContainer>
          </div>
          <div
            className="role-title col-xs-12 col-md-8"
            style={{ fontFamily: "KoHo" }}
          >
            <div className="left-align" style={{ color: "#990011FF" }}>
              {"<html>"}
            </div>
            <div className="left-align" style={{ color: "#232222" }}>
              {"<span> Welcome! </span>"}
            </div>
            <div className="left-align" style={{ color: "#232222" }}>
              {"<span> I'm a Lead Front-end Developer </span>"}
            </div>
            <div className="left-align" style={{ color: "#232222" }}>
              {"<span> With 10 years of experience </span>"}
            </div>
            <div className="left-align" style={{ color: "#232222" }}>
              {"<span> In BOTH Mobile & Web Development </span>"}
            </div>
            <div className="left-align" style={{ color: "#232222" }}>
              {"<span> About Me: </span>"}
            </div>
            <div className="left-align" style={{ color: "#232222" }}>
              {"<p> "}
              <span
                className="left-align italic"
                style={{ color: "#258392" }}
              ></span>
              <TypedText text={[this.description]}/>
              {" </p>"}
            </div>
            <div className="left-align" style={{ color: "#990011FF" }}>
              {"</html>"}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
