import React, { useState } from "react";

import "./ProjectsContainer.scss";
import * as ProjectModel from "../../models/project";
import TTTDemo from "../../components/ttt-demo/ttt-demo";

export default class ProjectsContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dimensions: {},
      projects: ProjectModel.getProjects(),
    };
    this.wrapper = React.createRef();
  }
  animateNow() {}
  /* Lifecycle Evenets */
  componentDidMount() {
    this.setState({
      dimensions: {
        height: window.screen.height,
      },
    });
  }
  render() {
    const { dimensions, projects } = this.state;
    return (
      <div
        id="projectsContainer"
        ref={this.wrapper}
        className="projects-container container-fluid "
      >
        <div className="row">
          <div className="common-title-container col-xs-12 col-md-8">
            <div
              className="project-title col-xs-12 col-md-8"
              style={{ fontFamily: "Dosis" }}
            >
              <div className="left-align h1" style={{ color: "#F65058FF" }}>
                Tic Tac Toe
              </div>
              <div className="left-align h3" style={{ color: "#FCF6F5FF" }}>
                Published easy to integrate <b>Tic Tac Toe Engine</b> library.
              </div>
              <div className="left-align h3" style={{ color: "#FCF6F5FF" }}>
                This Engine contains all of the business logic required for a
                player to interact with a computer.
                <b> EASY</b> and <b>HARD</b> gameplay options are currently
                available.
              </div>
              <div className="left-align h3" style={{ color: "#FCF6F5FF" }}>
                Available on all the 3 major platforms inlcuding React JS,
                Angular and VUE JS.
              </div>
              <div className="left-align h3" style={{ color: "#FCF6F5FF" }}>
                Core Library -{" "}
                <a href="https://www.npmjs.com/package/code-tic-tac-toe">NPM</a>
              </div>
              <div className="left-align h3" style={{ color: "#FCF6F5FF" }}>
                Demo Code for ReactJS, Angular and Vue -{" "}
                <a href="https://github.com/singh-inderpreet/Tic-Tac-Toe-Demo">
                  Github
                </a>
              </div>
              <div className="left-align h3" style={{ color: "#FCF6F5FF" }}>
                Tutorial -{" "}
                <a href="https://cutt.ly/Jnz4zQs">
                  Medium
                </a>
              </div>
            </div>
          </div>
          <div className="projects-content col-xs-12 col-md-4">
            <TTTDemo />
          </div>
        </div>
      </div>
    );
  }
}
